import { createContext, useState, useContext } from 'react';
import textFR from '../assets/text/var_text_fr.json';
import textEN from '../assets/text/var_text_en.json';

const LanguageContext = createContext();

export const LanguageProvider = ({ children }) => {
  const [language, setLanguage] = useState('FR');
  const toggleLanguage = () => {
    setLanguage(prev => (prev === 'FR' ? 'ENG' : 'FR'));
  };

  const text = language === 'FR' ? textFR : textEN;

  return (
    <LanguageContext.Provider value={{ language, toggleLanguage, text }}>
      {children}
    </LanguageContext.Provider>
  );
};

export const useLanguage = () => useContext(LanguageContext);
