import PathPDF from '../assets/files/MyCV.pdf';
import { useState } from 'react';
import { Menu, X } from 'lucide-react';
import { useLanguage } from './languageContext';
export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);
  const { language, toggleLanguage, text } = useLanguage();

  const scrollToSection = (id) => {
    const section = document.getElementById(id);
    const yOffset = -50;
    if (section) {
      const y = section.getBoundingClientRect().top + window.scrollY + yOffset;
      window.scrollTo({ top: y, behavior: 'smooth' });
      setIsOpen(false);
    }
  };

  return (
    <nav className="w-full backdrop-blur bg-neutral-950/80 h-20 flex items-center justify-between px-4 sm:px-8 md:px-16 lg:px-32 fixed top-0 z-50 text-white shadow-md font-montserrat">
      {/* TOGGLE LANGUAGE */}
      <div className="flex items-center space-x-2 cursor-pointer" onClick={toggleLanguage}>
        <span className={`text-sm font-bold ${language === 'ENG' ? 'text-white' : 'text-gray-500'}`}>ENG</span>
        <div className="w-10 h-5 bg-gray-700 rounded-full relative">
          <div
            className={`w-4 h-4 bg-white rounded-full absolute top-0.5 transition-transform duration-300 ${language === 'FR' ? 'translate-x-[22px]' : 'translate-x-0'}`}>
          </div>
        </div>
        <span className={`text-sm font-bold ${language === 'FR' ? 'text-white' : 'text-gray-500'}`}>FR</span>
      </div>

      {/* LIENS NAVIGATION - Desktop */}
      <div className="hidden md:flex space-x-10 text-sm sm:text-base items-center">
        <div onClick={() => scrollToSection("about")} className="hover:text-gray-300 cursor-pointer transition duration-300">{text.navbar.about}</div>
        <div onClick={() => scrollToSection("skills")} className="hover:text-gray-300 cursor-pointer transition duration-300">{text.navbar.skills}</div>
        <div onClick={() => scrollToSection("projects")} className="hover:text-gray-300 cursor-pointer transition duration-300">{text.navbar.project}</div>
        <a href={PathPDF} target="_blank" rel="noopener noreferrer"
      className="bg-white text-black px-5 py-2 rounded-full text-sm font-bold border-4 border-white hover:bg-gray-100 transition duration-300">
          {text.navbar.cv}
        </a>
      </div>
      
      {/* BOUTON MOBILE Hide */}
      <div className="md:hidden">
        <button onClick={() => setIsOpen(!isOpen)}
          className="p-3 bg-white text-black rounded-2xl shadow-lg transition duration-300 hover:shadow-2xl active:scale-95">
          <div className="transition-transform duration-300 ease-in-out">
            {isOpen ? (
              <X size={24} className="transition-all duration-300 ease-in-out" />
            ) : (
                <Menu size={24} className="transition-all duration-300 ease-in-out" />
              )}
          </div>
        </button>
      </div>

      {/* MENU MOBILE */}
      {isOpen && (
        <div className="absolute top-20 left-0 w-full bg-neutral-900/85 backdrop-blur-md text-white py-6 px-8 flex flex-col space-y-6 z-40 md:hidden shadow-lg rounded-b-xl border-t border-white/10">
          <div onClick={() => scrollToSection("about")} className="cursor-pointer hover:text-gray-300 transition">{text.navbar.about}</div>
          <div onClick={() => scrollToSection("skills")} className="cursor-pointer hover:text-gray-300 transition">{text.navbar.skills}</div>
          <div onClick={() => scrollToSection("projects")} className="cursor-pointer hover:text-gray-300 transition">{text.navbar.project}</div>
          <a href={PathPDF} target="_blank" rel="noopener noreferrer"
            className="w-fit bg-white text-black px-4 py-2 rounded-full text-sm font-bold border-4 border-white hover:bg-gray-100 transition text-center">
            {text.navbar.cv}
          </a>
        </div>
      )}
    </nav>
  );
}
